import { useEffect, useState } from 'react';

type Who = 'chris' | 'kate';
const localStorageKey = 'previous-who';

export default function Index() {
	const [who, setWho] = useState<Who | 'none'>('none');

	useEffect(() => {
		const previousWho = localStorage.getItem(localStorageKey);
		if (previousWho === 'chris' || previousWho === 'kate') {
			setWho(previousWho);
		}
	}, []);

	useEffect(() => {
		if (who) {
			localStorage.setItem(localStorageKey, who);
		}
	}, [who]);

	return (
		<div className="p-8">
			<header>
				<h1 className="text-3xl text-emerald-600 font-mono font-medium pb-4">
					Bookshelf
				</h1>
				<div>
					<label htmlFor="who">Who are you?</label>
					<select
						id="who"
						value={who}
						onChange={e => setWho(e.target.value as Who)}
						className="ml-2"
					>
						<option disabled value="none">
							Please choose
						</option>
						<option value="kate">Kate</option>
						<option value="chris">Chris</option>
					</select>
				</div>
			</header>
		</div>
	);
}
